@mixin altar-absolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: auto;
}

.color-blue {
  color: $colorBlue;
}

.progress {

  z-index: 20;
}

.text-little {
  font-size: 8px;
  font-family: Epilogue, sans-serif;
}

.altar{
  position: relative;
  width: 100%;
  height: 40vh;
}


.altar-img-1{
  @include altar-absolute;
  width: 75vw;
  z-index: 1;
  max-width: 300px;
}
.altar-img-1 img{
  //animation: turn 60s linear infinite;
}

.altar-img-2 {
  @include altar-absolute;
  width: 53vw;
  //animation-delay: 1s;
  z-index: 2;
  max-width: 200px;
}
.altar-img-2 img {
  //animation: turn-left 40s linear infinite;
}

.altar-img-3 {
  @include altar-absolute;
  width: 56vw;
  z-index: 3;
  max-width: 200px;
}
.altar-img-3 img {
  //animation: turn 60s linear infinite;
}

.altar-img-4 img{
  @include altar-absolute;
  width: 28vw;
  animation: slide 6s forwards;
  z-index: 5;
  transition: transform 0.6s ease 0.1s;
  transform-style: preserve-3d;
  max-width: 100px;
}

