@mixin show-text {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  color: #fff;
}

.start-btn {
  text-align: center;
  font-weight: 600;
  font-family: Epilogue, sans-serif;
  padding: 10px;
  border-radius: 15px;
  border: 2px solid #ddd;
  text-transform: uppercase;
}

.ne-text {
  font-weight: 600;
  font-family: Epilogue, sans-serif;
  padding: 6px 5px 2px;
  //width: 80vw;
  max-width: 300px;
  text-align: center;

}

.nonce-text {
  padding: 3px;
  //width: 80vw;
  font-weight: 600;
  text-align: center;
  font-family: Epilogue, sans-serif;
}

.nonce-span {
  color: #fff;
  text-shadow: 0 0 3px #fff;
  font-size: 12px;
  letter-spacing: 1px;
  font-family: Epilogue, sans-serif;
  //animation: nonce-an 1s linear infinite;

}

.hashrate-box{
  bottom: 20px;

  width: 80vw;
  max-width: 300px;
  padding: 10px;
  min-height: 150px;
  height: auto;
  z-index: 90;
  @include show-text
}

.hashrate-box-display{
  animation: bigAssButtonPulse 3s linear infinite alternate;
  text-shadow: 0 0 3px #0ff;
  font-size: 20px;
  letter-spacing: 5px;
  border: 1px solid #0ff;
  border-radius: 5px;
  box-shadow: 0 0 3px #0ff;
  padding: 10px 0;
  margin-bottom: 10px;
}

@keyframes nonce-an {
  from {
    opacity: 1;
  }
  to {
    opacity: 1;
    transform: translateY(10px);
  }
}

@keyframes bigAssButtonPulse {
  from {
    box-shadow: 0 0 15px #0ff;
  }
  to {
    box-shadow: 0 0 25px #f0f;
  }
}

@keyframes slide {
  from {
    //-webkit-transform: opacity(0);
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes turn {
  0% {
    -webkit-transform:rotate(0deg);
  }
  25% {
    -webkit-transform:rotate(90deg);
  }
  50% {
    -webkit-transform:rotate(180deg);
  }
  75% {
    -webkit-transform:rotate(270deg);
  }
  100% {
    -webkit-transform:rotate(360deg);
  }
}

@keyframes turn-left {
  0% {
    -webkit-transform:rotate(0deg);
  }
  25% {
    -webkit-transform:rotate(-90deg);
  }
  50% {
    -webkit-transform:rotate(-180deg);
  }
  75% {
    -webkit-transform:rotate(-270deg);
  }
  100% {
    -webkit-transform:rotate(-360deg);
  }
}

.epoch-modal{
  --width: 90%;
  --height: 80%;
  --border-radius: 15px;
  --background:#fff;
  overflow-y: scroll;
  --max-width: 600px;
  //opacity: 0.9;
}
.epoch-md{
  max-height: 85vh;
  overflow: scroll;
  padding-bottom: 80px;
}
.epoch-md .close{
  float: right;
  padding: 10px 12px 75px;
  //width: 100%;
}
.btn-bottom{
  width: 100%;
  position: absolute;
  bottom: 0;
  padding: 5px;
  background: #fff;
}

