  min-height: 50px;
  margin: 12px 0 0;
  padding: 3px;
  font-size: 12px;
  text-align: center;
}


@mixin chaos-absolute {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin chaos-element{
  border: #fff;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  box-shadow:$show-customer;
}

