.chaos div:nth-child(1){
  @include chaos-absolute;
  border: #fff;
  width: 70vw;
  height: 70vw;
  max-width: 400px;
  max-height: 400px;
  background: $color-boll;
  box-shadow: 0 0 25px 0px #edcc67;
  border-radius: 50%;
}

.chaos div:nth-child(1) div{
  @include chaos-absolute;
  @include chaos-element;
}

.chaos div:nth-child(1) div:nth-child(12){
  @include chaos-absolute;
  border: #fff;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  box-shadow:
          -30vw 13vh 0px 0.75px $color-boll,
          -23vw 12vh 0px 0.75px $color-boll,
          -27vw 10vh 0px 0.75px $color-boll,
          -31vw 9vh 0px -2.25px $color-boll,
          -33vw 6vh 0px -4.25px $color-boll,
          -34vw 10vh 0px -4.25px $color-boll,
          -38vw 12vh 0px -5.25px $color-boll,
          -33vw 11vh 0px -5.25px $color-boll,
          -31vw 11vh 0px -6.25px $color-boll,
          -34vw 7vh 0px -3.25px $color-boll,
          -36vw 12vh 0px -6.25px $color-boll;
