}

.chaos-light{
  position: absolute;
  top: 70%;
  left: 10%;
  width: 64px;
  height: 64px;
  box-shadow: 0 0 20px 0px #edcc67;
  border-radius: 50%;
  transform: translate(-10%,-70%);
}

.chaos-dark{
  position: absolute;
  top: 70%;
  left: 90%;
  width: 64px;
  height: 64px;
  box-shadow: 0 0 20px 0px #edcc67;
  border-radius: 50%;
  transform: translate(-90%,-70%);
}

.chaos div:nth-child(2) img{
  @include chaos-absolute;
  width: 38vw;
  max-width: 300px;
  animation: slide 6s forwards;
}

.chaos div:nth-child(3) img{
  @include chaos-absolute;
  width: 38vw;
  max-width: 300px;
  animation: slide 6s forwards;
}


@keyframes star-move-out {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  20% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  99% {
    opacity: 0.5;
  }
  100% {
    transform: translateX(100vw) translateY(-20vh) scale(0.2) ;
    opacity: 0;
  }
}

div.word {
  position: absolute;
  right: 1vw;
  text-align: right;
  font-size: 2vh;
  animation: word-appear 60s infinite;
  opacity: 0;
  transform: translate3d(calc(1vw + 100%), 0, 0);
}

@keyframes word-appear {
  0% {
    opacity: 0;
    transform: translate3d(calc(1vw + 100%), 0, 0);
  }
  2% {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  80% {
    opacity: 0;
    transform: translate3d(0, 0, 0);
  }
}


